  .home_page {
    background: linear-gradient(59deg, rgb(158, 185, 232) 0%,  rgb(185, 67, 122) 100%); 
    height: 10vh;
    color: white;
  }
  .home_page p {
    font-size: 1rem;
  }

.login_container{
    height: 60vh;
    width: 80vw;
    text-align: center;
    margin: 55px auto 0px auto;
}

.email{
    margin-top: 15px;
    width: 50%;
    height: 34px;
}
.password{
    margin-top: 15px;
    width: 100%;
    height: 34px;
}
.btn{
    margin-top: 15px;
    width: 50%;
    height: 34px;
    background-color: rgb(79 70 229);
    color: white;
    border: none;
    border-radius: 7px;
    cursor: pointer;
}


/* Mobile Styles for devices with max-width of 320px */
@media (max-width: 320px) {
  .email{
    width: 90%;
  }
  .btn{
    width: 90%;
  }
}


/* Mobile Styles for devices with max-width of 412px but more than 320px */
@media (min-width: 321px) and (max-width: 412px) {
.email{
  width: 90%;
}
.btn{
  width: 90%;
}
}


