.bgGray900 {
    background-color: #1a202c;
  }
  
  .maxW2xl {
    max-width: 32rem;
    margin: 0 auto;
    color: #fff;
    padding: 2.5rem 0;
  }
  
  .textCenter {
    text-align: center;
  }
  
  .text3xl {
    font-size: 1.875rem;
    margin-bottom: 0.75rem;
  }
  
  .flexCenter {
    display: flex;
    justify-content: center;
    margin: 2.5rem 0;
  }
  
  .flexItemsCenter {
    display: flex;
    align-items: center;
  }
  
  .border {
    border: 1px solid #e2e8f0;
  }
  
  .roundedLg {
    border-radius: 0.5rem;
  }
  
  .px4 {
    padding-left: 1rem;
    padding-right: 1rem;
  }
  
  .py2 {
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
  }
  
  .w52 {
    width: 13rem;
    margin: 0.5rem;
  }
  
  .w44 {
    width: 11rem;
    margin: 0.5rem;
  }
  
  .mx2 {
    margin-left: 0.5rem;
    margin-right: 0.5rem;
  }
  
  .img {
    width: 1.75rem;
  }
  
  .textLeft {
    text-align: left;
    margin-left: 0.75rem;
  }
  
  .textXs {
    font-size: 0.75rem;
    color: #edf2f7;
  }
  
  .textSm {
    font-size: 0.875rem;
  }
  
  .footer {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    font-size: 0.875rem;
    color: #a0aec0;
  }
  
  .footerText {
    order: 2;
  }
  
  .footerLinks {
    order: 1;
    display: flex;
    justify-content: center;
    margin-bottom: 2rem;
  }
  
  .link {
    padding: 0 0.5rem;
  }
  
  .borderLeft {
    border-left: 1px solid #a0aec0;
  }
  