
.button {
  padding: 0.5rem 1rem;
  background-color: #4f46e5;
  color: white;
  border-radius: 0.25rem;
  border: none;
  cursor: pointer;
  margin-left: 2rem;
}

.nav_container {
  background-color: rgb(114, 114, 245);
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.logo_section {
  display: flex;
  align-items: center;
}
.company_name {
  color: white;
  color: greenyellow;
  font-size: 1.4rem;
  margin-left: 14px;
}
.Navbar {
  display: flex;
  align-items: center;

}
.Navbar a {
  text-decoration: none;
  color: white;
  margin-right: 23px;
  font-size: 1.1rem;
  height: 10vh;
  display: flex;
  align-items: center;
  gap: 5px;
}

.cartNotification{
  width: 15px;
  margin-left: -8px;
  margin-bottom: 29px;
  background-color: red;
  color: white;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  font-size: 0.9rem;
  cursor: pointer;
}

/* Mobile Styles for devices with max-width of 320px */
@media (max-width: 320px) {
  .nav_container {
    height: 15vh;
    flex-direction: column;
    font-size: 1rem;
  }

  .Navbar {
    height: auto;
    margin: auto;
    font-size: 0.75rem;
  }

  .Navbar a {
    margin-right: 5px;
  }
}

/* Mobile Styles for devices with max-width of 412px but more than 320px */
@media (min-width: 321px) and (max-width: 412px) {
  .nav_container {
    flex-direction: column;
    font-size: 1rem;
  }

  .Navbar {
    margin: auto;
    font-size: 1.06rem;
  }

  .Navbar a {
    margin-right: 5px;
    font-size: 1rem;
  }
    .logo_section{
    display: flex;
    flex-direction: column;
  }
  .button{
    margin-top: 0.5rem;
  }
}


@media (min-width: 413px) and (max-width: 758px) {
  .nav_container {
    flex-direction: column;
    font-size: 1rem;

  }

  .Navbar {
    margin: auto;
    font-size: 1.1rem;
  }

  .Navbar a {
    margin-right: 5px;
  }

  .company_name{
    font-size: 2.1em;
  }
}
